import formatMessage from "format-message";
import React from "react";
import Header from "components/Header";
import {CssSizeEnum} from "enums/CssSizeEnum";

type Props = {
  /**
   * Callback when user close the window.
   */
  onWindowCloseClick: () => void,
  /**
   * Indicate the current version for of app.
   */
  appVersion: string
};

export const About = (props: Props) => {
  const {onWindowCloseClick, appVersion} = props;

  return (
    <div onClick={onWindowCloseClick} className="st_about">
      <Header size={CssSizeEnum.Large} title={formatMessage("about")}/>
      <p className="st_aboutVersion">{formatMessage("Version")}: {appVersion}</p>
    </div>
  );
};

About.displayName = "App/About";

export default About;