import classNames from "classnames";
import formatMessage from "format-message";
import React from "react";
import {AlignEnum} from "enums/AlignEnum";
import {CssSizeEnum} from "enums/CssSizeEnum";
import LogoDashThis from "medias/svg/dashthis-logo.inline.svg";
import {Icon, Icons} from "@dt/ui/dist/controls/Icon";
import {Cls} from "style";

type HeaderComponentProps = {
  /**
   * Header content alignment.
   */
  align: AlignEnum;
  /**
   * The size on header.
   * change the size of header will change the font size.
   */
  size: CssSizeEnum;
  /**
   * Add extra css class to this component.
   */
  className: Cls;
  /**
   * The title.
   */
  title: string;
} & typeof defaultProps;

const defaultProps = {
  align: AlignEnum.Center,
  size: CssSizeEnum.Normal,
  className: null
};
const HeaderComponent = (props: HeaderComponentProps) => {
  const {title, align, className, size} = props;
  const componentClass = classNames("st_header",
    {
      "st_header--left": true
    }, `st_header--${align}`,
    className,
    `st_header--${size}`
  );

  return (
    <div className={componentClass}>
      <LogoDashThis className="st_headerLogo"/>
      <div className="st_headerContent">
        <h1 className="st_headerTitle">{formatMessage("DashThis Studio")}</h1>
        <Icon icon={Icons.Chevron} className="st_headerChevron"/>
        <p className="st_headerSection">{title}</p>
      </div>
    </div>
  );

};

HeaderComponent.displayName = "Components.Header";
HeaderComponent.defaultProps = defaultProps;

export default HeaderComponent;
